<template>
  <b-card>
    <b-row class="pl-5 pr-5">
      <b-col col="3">
        <p>ID: {{ userData.details_user.id }}</p>
        <p>Email: {{ userData.details_user.email }}</p>
        <p>Role: {{ userData.details_user.role }}</p>
        <p>Status: {{ userData.details_user.state }}</p>
        <p v-if="inputState">Status: <b-form-select v-model="userData.details_user.state" :options="options_state"></b-form-select></p>
        <p>Claim Airdrop: {{ userData.details_user.is_claimed }}</p>
        <p>Telegram: {{ userData.details_user.telegram }}</p>
        <p>Binance Pay ID: {{ userData.details_user.binance_pay_id }}</p>
        <p v-if="!inputState">Withdrawal Address: {{ userData.details_user.binance_bep20_address ? userData.details_user.binance_bep20_address : 'Belum Submit' }}</p>
        <b-form class="mb-3" v-if="inputState">
          <b-form-group id="input-group-1" label="Withdrawal Address" label-for="input-1">
            <b-form-input id="input-1" :placeholder="edited_user.binance_bep20_address" v-model="edited_user.binance_bep20_address" :disabled="false"></b-form-input>
          </b-form-group>
        </b-form>
        <p>Deposit Address Address: {{ userData.details_user.balance.deposit_address }}</p>
        <p>Refferal Code: {{ userData.details_user.reff_code }}</p>
        <p v-if="!inputState">leader: {{ userData.details_user.is_leader }}</p>
        <p v-if="inputState">leader: <b-form-select v-model="edited_user.is_leader" :options="options_leader"></b-form-select></p>
        <!-- <b-col class="hp-flex-none w-auto">
          test
          <b-form-select v-model="selected" :options="options"></b-form-select>
        </b-col> -->
        <div v-if="inputState">
          <b-button variant="primary-2" class="mt-5" @click="updateSetting"><i class="ri-2x ri-save-line" style="font-size: 24px" />Submit</b-button>
        </div>
        <div v-else>
          <b-button
            variant="primary"
            @click="enableInput(userData.details_user.id, userData.details_user.is_leader, userData.details_user.binance_bep20_address, userData.details_user.state)"
            ><i class="ri-2x ri-edit-2-line" style="font-size: 24px" />Edit</b-button
          >
        </div>
      </b-col>
      <b-col col="9">
        <p>Balance: {{ parseInt(userData.details_user.balance.balance).toLocaleString() }} SPC</p>
        <p>Staked Balance: {{ parseInt(userData.details_user.balance.staked_balance).toLocaleString() }} SPC</p>
        <p>Airdrop Balance: {{ parseInt(userData.details_user.balance.airdrop_balance).toLocaleString() }} SPC</p>
        <p>Total Buy: {{ parseInt(userData.total_buy).toLocaleString() }} SPC</p>
        <p>Total Sell: {{ parseInt(userData.total_sell).toLocaleString() }} SPC</p>
        <p>Total Earn: {{ parseInt(userData.total_earn).toLocaleString() }} SPC</p>
        <p>Total Refferal Earning: {{ userData.total_earn_reff.toLocaleString() }} SPC</p>
        <p>Created At: {{ new Date(userData.details_user.created_at).toLocaleString('id-ID', { timeZone: 'Asia/Jakarta' }) }}</p>
        <p>
          Upline Email:
          {{ userData.upline_email }}
        </p>
        <p>
          Upline Id:
          <a :href="`/user_detail/${userData.upline_id}`" target="_blank"> {{ userData.upline_id }} (klik to detail user) </a>
        </p>
      </b-col>
    </b-row>
  </b-card>
</template>

<script>
import { BCard, BContainer, BRow, BTabs, BCol, BInputGroup, BFormInput, BFormSelect, BForm, BFormGroup, BButton } from 'bootstrap-vue';
import { mapGetters, mapState, mapActions } from 'vuex';

export default {
  computed: {
    ...mapGetters('detailStore', ['userData']),
    ...mapState('detailStore', ['edited_user', 'options_leader', 'options_state']),
  },
  methods: {
    ...mapActions('detailStore', ['updateUser']),
    enableInput(_user_id, _leader_state, _address_wd, _roles) {
      console.log(_user_id, _leader_state, _address_wd, _roles);
      this.inputState = true;
      this.edited_user.id_user = _user_id;
      this.edited_user.is_leader = _leader_state;
      this.edited_user.binance_bep20_address = _address_wd ? _address_wd : '';
      this.edited_user.state = _roles;
    },
    async updateSetting() {
      await this.updateUser();
      this.inputState = false;
    },
  },
  data() {
    return {
      selected: 'true',
      inputState: false,
      selected_leader: 'true',
      options: [
        { value: 'true', text: 'true' },
        { value: 'false', text: 'false' },
      ],
    };
  },

  components: {
    BTabs,
    BRow,
    BContainer,
    BCol,
    BCard,
    BInputGroup,
    BFormInput,
    BForm,
    BFormGroup,
    BButton,
    BFormSelect,
  },
};
</script>
