<template>
  <b-card class="hp-project-table-card hp-analytics-project-table-card mb-5">
    <b-row align-v="center" align-h="between">
      <b-col cols="12" class="mb-3">
        <b-row align-v="center" align-h="between">
          <b-col cols="12" md="6">
            <h3>Refferal List</h3>
            <p class="hp-p1-body mb-0">You can see refferal list at here :</p>
          </b-col>
          <b-col class="hp-flex-none w-auto">
            <b-form-select v-model="selected" :options="options"></b-form-select>
          </b-col>
        </b-row>
      </b-col>
    </b-row>
    <div v-if="listData.length === 0">No Data To Display</div>
    <div v-else>
      <b-table-simple responsive borderless class="mb-0">
        <b-thead>
          <b-tr>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Date</span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Type</span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Email </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Level </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Amount </span>
            </b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr :key="index" v-for="(item, index) in listData">
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                {{ new Date(item.created_at).toLocaleString('id-ID', { timeZone: 'Asia/Jakarta' }) }}
              </span>
            </b-td>

            <b-td>
              <span class="mb-0 font-weight-medium text-black-100 hp-text-color-dark-0" v-if="item.transaction_type === 'staking_earning'"> Matching Reward </span>
              <span class="mb-0 font-weight-medium text-black-100 hp-text-color-dark-0" v-if="item.transaction_type === 'staking_join'"> Staking Join </span>
              <span class="mb-0 font-weight-medium text-black-100 hp-text-color-dark-0" v-if="item.transaction_type === 'trade'"> Trade Reward </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30"> {{ item.email }} </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                {{ item.level_ref }}
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                <span class="mb-0 text-black-80 hp-text-color-dark-30"> {{ parseFloat(item.amount).toLocaleString() }} SPC </span>
              </span>
            </b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
      <ul role="menubar" aria-disabled="false" aria-label="Pagination" class="pagination b-pagination ml-5 mt-3">
        <li role="presentation" aria-hidden="true" class="page-item">
          <span role="menuitem" aria-label="Go to previous page" aria-disabled="true" class="page-link" @click="prevPage">⏪</span>
        </li>
        <li role="presentation" aria-hidden="true" class="page-item mt-2">Page {{ pageData }}</li>
        <li role="presentation" aria-hidden="true" class="page-item">
          <span role="menuitem" aria-label="Go to next page" aria-disabled="true" class="page-link" @click="nextPage">⏩</span>
        </li>
      </ul>
    </div>
  </b-card>
</template>

<script>
import { BAlert, BPagination, BFormInput, BLink, BFormSelect, BButton, BRow, BCol, BCard, BBadge, BTableSimple, BThead, BTbody, BTr, BTh, BTd } from 'bootstrap-vue';
import { mapActions, mapGetters } from 'vuex';

export default {
  data() {
    return {
      selected: 'all',
      options: [
        { value: 'trade', text: 'trade' },
        { value: 'staking_join', text: 'staking' },
        { value: 'staking_earning', text: 'matching' },
        { value: 'all', text: 'all' },
      ],
    };
  },
  watch: {
    async selected(newValue, oldValue) {
      if (newValue === 'trade') {
        await this.changeType('trade');
        await this.getList();
      }
      if (newValue === 'staking_join') {
        await this.changeType('staking_join');
        await this.getList();
      }
      if (newValue === 'staking_earning') {
        await this.changeType('staking_earning');
        await this.getList();
      }
      if (newValue === 'all') {
        await this.changeType('all');
        await this.getList();
      }
    },
  },

  computed: {
    ...mapGetters('detailEarnStore', ['listData', 'pageData', 'typeData', 'levelData']),
  },
  methods: {
    ...mapActions('detailEarnStore', ['getList', 'getPrevData', 'getNextData', 'changeType']),
    async prevPage() {
      await this.getPrevData();
    },
    async nextPage() {
      await this.getNextData();
    },
  },
  async mounted() {
    await this.getList();
  },

  components: {
    BRow,
    BCol,
    BCard,
    BBadge,
    BTableSimple,
    BThead,
    BTbody,
    BTr,
    BTh,
    BTd,
    BButton,
    BFormSelect,
    BFormInput,
    BLink,
    BPagination,
    BAlert,
  },
};
</script>
