<template>
  <b-card class="hp-project-table-card hp-analytics-project-table-card mb-5">
    <b-row align-v="center" align-h="between">
      <b-col cols="12" class="mb-3">
        <b-row align-v="center" align-h="between">
          <b-col cols="12" md="6">
            <h3>Leader Reward</h3>
            <p class="hp-p1-body mb-0">You can see leader Reward at here :</p>
          </b-col>
          <b-col class="hp-flex-none w-auto">
            <b-form-select v-model="selected" :options="options"></b-form-select>
          </b-col>
        </b-row>
      </b-col>
      <b-col cols="12" md="12" class="mt-5">
        <div v-if="pendingCLaim" class="mb-5">
          <b-col class="mb-3">
            <b-td>
              <span class="mb-0 font-weight-medium text-black-100 hp-text-color-dark-0"> PENDING CLAIM : {{ parseInt(sumData).toLocaleString() }} SPC </span>
            </b-td>
          </b-col>
          <b-col>
            <b-td>
              <span class="mb-0 mr-5 text-black-80 hp-text-color-dark-30">
                <b-button variant="primary" class="btn-ghost" @click="approve()"> APPROVE</b-button>
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 ml-5text-black-80 hp-text-color-dark-30">
                <b-button variant="danger" class="btn-ghost" @click="reject()"> REJECT</b-button>
              </span>
            </b-td>
          </b-col>
        </div>
      </b-col>
    </b-row>
    <div v-if="rewardList.length === 0">No Data To Display</div>
    <div v-else>
      <b-table-simple responsive borderless class="mb-0">
        <b-thead>
          <b-tr>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> Date</span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> referral_id</span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> refferal_email </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> trade_id </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> amount </span>
            </b-th>
            <b-th>
              <span class="hp-badge-size font-weight-normal text-black-60 hp-text-color-dark-50 text-uppercase"> status </span>
            </b-th>
          </b-tr>
        </b-thead>
        <b-tbody>
          <b-tr :key="index" v-for="(item, index) in rewardList">
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                {{ new Date(item.created_at).toLocaleString('id-ID', { timeZone: 'Asia/Jakarta' }) }}
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30"> {{ item.referral_id }} </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                {{ item.reff_email }}
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                <span class="mb-0 text-black-80 hp-text-color-dark-30"> {{ item.refference_id }} </span>
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                <span class="mb-0 text-black-80 hp-text-color-dark-30"> {{ parseInt(item.amount).toLocaleString() }} SPC </span>
              </span>
            </b-td>
            <b-td>
              <span class="mb-0 text-black-80 hp-text-color-dark-30">
                <span v-if="item.claim_state == 0" class="mb-0 text-black-80 hp-text-color-dark-30"> unclaimed </span>
                <span v-if="item.claim_state == 1" class="mb-0 text-black-80 hp-text-color-dark-30"> pending claim </span>
                <span v-if="item.claim_state == 2" class="mb-0 text-black-80 hp-text-color-dark-30"> claimed </span>
                <span v-if="item.claim_state == 10" class="mb-0 text-black-80 hp-text-color-dark-30"> rejected </span>
              </span>
            </b-td>
          </b-tr>
        </b-tbody>
      </b-table-simple>
      <!-- <ul role="menubar" aria-disabled="false" aria-label="Pagination" class="pagination b-pagination ml-5 mt-3">
        <li role="presentation" aria-hidden="true" class="page-item">
          <span role="menuitem" aria-label="Go to previous page" aria-disabled="true" class="page-link" @click="prevPage">⏪</span>
        </li>
        <li role="presentation" aria-hidden="true" class="page-item mt-2">Page {{ pageData }}</li>
        <li role="presentation" aria-hidden="true" class="page-item">
          <span role="menuitem" aria-label="Go to next page" aria-disabled="true" class="page-link" @click="nextPage">⏩</span>
        </li>
      </ul> -->
    </div>
  </b-card>
</template>

<script>
import { BAlert, BPagination, BFormInput, BLink, BFormSelect, BButton, BRow, BCol, BCard, BBadge, BTableSimple, BThead, BTbody, BTr, BTh, BTd } from 'bootstrap-vue';
import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  data() {
    return {
      selected: 'pending',
      pending_state: false,
      options: [
        { text: 'pending', value: 'pending' },
        { text: 'all', value: 'all' },
        { text: 'rejected', value: 'rejected' },
      ],
    };
  },
  computed: {
    ...mapGetters('leaderStore', ['rewardList', 'pendingCLaim', 'pendingData', 'sumData']),
    ...mapState('leaderStore', ['data_leader','pending_claim']),
  },
  watch: {
    async selected(newValue, oldValue) {
      if (newValue === 'all') {
        await this.getAllData();
      }
      if (newValue === 'pending') {
        await this.getPendingData();
      }
      if (newValue === 'rejected') {
        await this.getRejectData();
      }
    },
  },
  methods: {
    ...mapActions('leaderStore', ['getPerLeaderData', 'setUserId', 'getPendingData', 'approveReward', 'reject', 'getRejectData', 'getAllData','setPending']),
    async prevPage() {
      await this.getPrevData();
    },
    async nextPage() {
      await this.getNextData();
    },
    async approve() {
      await this.setPending()
      await this.approveReward()
    },
    async reject() {
      await this.setPending()
      await this.rejectReward()
    },
  },
  async mounted() {
    await this.setUserId(this.$route.params.id_user);
    await this.getPendingData();
  },

  components: {
    BRow,
    BCol,
    BCard,
    BBadge,
    BTableSimple,
    BThead,
    BTbody,
    BTr,
    BTh,
    BTd,
    BButton,
    BFormSelect,
    BFormInput,
    BLink,
    BPagination,
    BAlert,
  },
};
</script>
