<template>
  <b-row class="mb-32">
    <b-col cols="12">
      <b-row>
        <b-col class="flex-grow-1 overflow-hidden">
          <b-row>
            <b-col cols="12" class="mb-32">
              <h1 class="hp-mb-0">User Details</h1>
            </b-col>
            <b-col cols="12">
              <table-list-user />
            </b-col>
          </b-row>
        </b-col>
      </b-row>
    </b-col>
  </b-row>
</template>

<script>
import { BRow, BCol } from 'bootstrap-vue';

import TableListUser from './TabUser.vue';
import { mapActions, mapGetters, mapState } from 'vuex';

export default {
  components: {
    BRow,
    BCol,
    TableListUser,
  },
  methods: {
    ...mapActions('detailStore', ['getUser', 'updateId']),
    ...mapActions('historyStakeStore', ['changeId']),
    ...mapActions('historyTradeStore', ['setId']),
    ...mapActions('detailReffStore', ['userId']),
    ...mapActions('detailEarnStore', ['upId']),
  },
  async created() {
    await this.updateId(this.$route.params.id_user);
    await this.changeId(this.$route.params.id_user);
    await this.setId(this.$route.params.id_user);
    await this.userId(this.$route.params.id_user);
    await this.upId(this.$route.params.id_user);
  },
  async mounted() {
    await this.getUser();
  },
};
</script>
